@import url(https://fonts.googleapis.com/css?family=Josefin+Sans);


a {
  text-decoration: none !important;
}

.cart {
  color: white;
  font-size: 1.5rem;
}

.footer {
    text-align: center;
    color: white;
    background-color: rgb(51, 50, 50);
    height: 3rem;
    padding: 1rem 0 3rem 0;
    margin: 0;
}

body {
    font-family: "Josefin Sans", cursive;
}

.itemList {
  display: inline-block;
  text-align: center;
}

.itemDetail {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    margin-bottom: 8rem;
}

@media (max-width: 576px) {
    .itemDetail {
        display: block;
        text-align: center;
    }
}

#hideElement {
    display: none;
}

.itemPrice {
    font-size: 1.5rem;
}

.category {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-around;
            justify-content: space-around;
}

.category-border {
    padding-top: 4rem;
    text-align: center;
    width: 20rem;
    height: 20rem;
    border: red 0.15rem solid;
    border-radius: 1rem;
    margin-bottom: 8rem;
}

@media (max-width: 576px) {
    .category {
        display: block;
    }
    .category-border {
        margin: 2rem auto;
    }
}

label {
    font-weight: 700;
}

input {
    padding: 0.4rem;
    display: block;
    width: 100%;
    background-color: #e1e1e1;
    margin-bottom: 1rem;
    border: none;
    border-radius: 1rem;
}

